import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { EstructuraLegislativaPage } from './estructura-legislativa.page';
import { ComponentsModule } from '../components.module';

const routes: Routes = [
  {
    path: '',
    component: EstructuraLegislativaPage,

      children:
          [
              {
                  path: 'tabAmbito',
                  children:
                      [
                          {
                              path: '',
                              loadChildren: '../tabs/tab-ambito/tab-ambito.module#TabAmbitoPageModule'
                          }
                      ]
              },
              {
                  path: 'tabComision',
                  children:
                      [
                          {
                              path: '',
                              loadChildren: '../tabs/tab-comision/tab-comision.module#TabComisionPageModule'
                          }
                      ]
              },
              {
                  path: '',
                  redirectTo: 'tabComision',
                  pathMatch: 'full'
              }
          ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes),
      ComponentsModule
  ],
    exports: [
        RouterModule
    ],
  declarations: [EstructuraLegislativaPage]
})
export class EstructuraLegislativaPageModule {}
